.popup__title {
  margin: 0;
  padding: 0;
  margin-bottom: 14px;
  font-family: 'Inter', 'Arial', sans-serif;
  font-weight: 900;
  font-style: normal;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}

@media screen and (max-width: 568px){
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 40px;
  }
}